.page {
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
    background-color: rgba(29, 29, 29, 1);
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;


    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

}
